import { React, Fragment, useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { addEducation } from '../../actions/profile';
import { Link, useNavigate } from 'react-router-dom';

const AddEducation = ({ addEducation }) => {
	const [formData, setFormData] = useState({
		school: '',
		degree: '',
		fieldofstudy: '',
		form: '',
		to: '',
		current: false,
		description: '',
	});
	const {
		school,
		degree,
		fieldofstudy,
		from,
		to,
		current,
		description,
	} = formData;
	const navigate = useNavigate();
	const onChange = (e) =>
		setFormData({ ...formData, [e.target.name]: e.target.value });

	const [toDateDisabled, toggleToDateDisable] = useState(false);

	return (
		<Fragment>
			<section className='container'>
				<h1 className='large text-primary'>Add Your Education</h1>
				<p className='lead'>
					<i className='fas fa-code-branch'></i> Add any school/Boot Camp that
					you have attended
				</p>
				<small>* = required field</small>
				<form
					className='form'
					onSubmit={(e) => {
						e.preventDefault();
						addEducation(formData).then(() => navigate('/dashboard'));
					}}
				>
					<div className='form-group'>
						<input
							type='text'
							placeholder='* Degree Title'
							name='degree'
							value={degree}
							required
							onChange={(e) => onChange(e)}
						/>
					</div>
					<div className='form-group'>
						<input
							type='text'
							placeholder='* school'
							name='school'
							value={school}
							required
							onChange={(e) => onChange(e)}
						/>
					</div>
					<div className='form-group'>
						<input
							type='text'
							placeholder='Feild Of Study'
							name='fieldofstudy'
							value={fieldofstudy}
							onChange={(e) => onChange(e)}
						/>
					</div>
					<div className='form-group'>
						<h4>From Date</h4>
						<input type='date' name='from' value={from} onChange={onChange} />
					</div>
					<div className='form-group'>
						<p>
							<input
								type='checkbox'
								name='current'
								value={current}
								onChange={() => {
									setFormData({ ...formData, current: !current });
								}}
								onClick={() => {
									toggleToDateDisable(!toDateDisabled);
								}}
							/>{' '}
							Current School
						</p>
					</div>
					{!toDateDisabled && (
						<div className='form-group'>
							<h4>To Date</h4>
							<input
								type='date'
								name='to'
								value={to}
								onChange={(e) => onChange(e)}
							/>
						</div>
					)}
					<div className='form-group'>
						<textarea
							name='description'
							cols='30'
							rows='5'
							placeholder='Job Description'
							value={description}
							onChange={(e) => onChange(e)}
						></textarea>
					</div>
					<input type='submit' className='btn btn-primary my-1' />
					<Link className='btn btn-light my-1' to='/dashboard'>
						Go Back
					</Link>
				</form>
			</section>
		</Fragment>
	);
};

AddEducation.propTypes = {
	addExperience: PropTypes.func.isRequired,
};

export default connect(null, { addEducation })(AddEducation);
